<template>
    <b-container fluid>
<!--       <pre>{{product}}</pre>-->
      <b-overlay :show="!productLoaded" spinner-large
        spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
        <b-container fluid class="mx-auto text-center" v-if="productLoaded">
            <p class="ml-4 mt-2 mb-n1">{{ product.name }}</p>
            <b-form-rating inline no-border
                variant="warning"
                readonly
                v-model="product.rating"
                precision="1">
            </b-form-rating>
        </b-container>
        <b-container fluid>
            <b-row>
                <b-col cols="12" md="4">
                    <b-carousel
                        id="carousel-1"
                        :interval="4000"
                        controls
                        indicators
                        img-width="200"
                        img-height="200"
                        style="text-shadow: 1px 1px 2px #333;"
                        @sliding-start="onSlideStart"
                        @sliding-end="onSlideEnd"
                    >
                      <b-carousel-slide
                        v-for="(image,index) in product.images" :key="index">
                        <template v-slot:img>
                          <img width="200"
                            height="300"
                            :src="image.link"
                            alt="Product Images"
                            class="d-block w-100"
                          >
                        </template>
                      </b-carousel-slide>
                    </b-carousel>
                </b-col>
                <b-col cols="12" md="8" class="d-flex flex-column mx-auto mt-2 mt-md-0" v-if="productLoaded">
                    <b-table-simple bordered striped>
                      <b-thead>
                        <b-tr>
                          <!-- <b-th>Type</b-th>
                          <b-th>C</b-th> -->
                        </b-tr>
                      </b-thead>
                      <b-tbody>
                        <b-tr>
                          <b-th class="mx-auto my-auto text-center">Specifications</b-th>
                          <b-td >
                            <ul v-for="([s,values],i) in Object.entries(product.specifications)" :key="i">{{s}}
                              <template v-if="Array.isArray(values)">
                              <b-list-group v-for="(spec,index) in values" :key="index" class="ml-5">
                                  <li>{{spec}}</li>
                              </b-list-group>
                              </template>
                              <template v-else><li style="list-style-type: circle;" class="ml-5">{{values}}</li></template>
                            </ul>
                          </b-td>
                        </b-tr>
                        <b-tr>
                          <b-th class="mx-auto my-auto text-center">Price</b-th>
                          <b-td class="ml-5">
                            <span id="prev-price" class="ml-5">
                                <span class="strike">&#2547;{{ Math.round(product.price.sellingPrice / (1 -  product.price.discount/ 100)) }}</span>
                                <strong id="discount">&nbsp;-{{ product.price.discount }}%</strong>
                            </span>
                            <br>
                            <span class="ml-5" id="cur-price">&nbsp;&nbsp;&#2547;{{product.price.sellingPrice}}</span>
                          </b-td>
                        </b-tr>
                        <b-tr>
                          <b-th class="mx-auto my-auto text-center">Warrenty</b-th>
                          <b-td>
                            <ul class="ml-3" style="list-style-type: square">
                                <li v-if="product.price.warranty!=null">{{product.price.warranty}} days return</li>
                                <li v-if="product.price.refundable">100% refund</li>
                            </ul>
                          </b-td>
                        </b-tr>
                      </b-tbody>
                    </b-table-simple>
                </b-col>
            </b-row>
            <div class="mx-auto text-center col-md-4" v-if="productLoaded">
              <div>
                <b-button
                  id="add-to-cart"
                  :aria-controls="'sidebar-right-footer'+product.productID"
                  :aria-expanded="show"
                  @click="initialHandler(true)"
                >
                  Add to Cart
                </b-button>
                <b-button
                  id="buy-now"
                  :aria-controls="'sidebar-right-footer-buy'+product.productID"
                  aria-expanded=""
                  @click="initialHandler(false)"
                >
                  Buy Now
                </b-button>
                <b-sidebar
                  :id="'sidebar-right-footer'+product.productID"
                  aria-label="Sidebar with custom footer"
                  v-model="show"
                  shadow
                  lazy
                >
                  <!-- <pre>{{this.product.specifications}}</pre>
                  <pre>{{selector}}</pre> -->
                  <template v-slot:footer>
                    <div class="mx-auto text-center">
                      <b-button squared variant="warning" @click="doReset" class="col-6">Reset</b-button>
                      <b-button @click="finalHandler" squared variant="success" class="col-6" :disabled="!allSelected">
                        {{cartButtonPressed ? 'Add to Cart' : 'Check Out'}}
                      </b-button>
                    </div>
                  </template>
                  <b-overlay :show="!productLoaded" opacity="0.4" rounded="sm" style="height:500px">
                    <template v-slot:overlay>
                      <div class="text-center">
                        <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
                        <p id="cancel-label">Please wait...</p>
                      </div>
                    </template>
                    <div class="mx-auto d-flex flex-column" v-if="productLoaded">
                      <b-list-group>
                        <span
                          v-for="(values, key, index) in product.specifications"
                          :key="index">
                          <b-list-group-item v-if="Array.isArray(values)">
                            <b-form-group :label="key">
                            <b-form-select style="width:70%"
                                          v-model="selector[key]" :options="values" :value-field="key">
                              <template v-slot:first>
                                <b-form-select-option :value="null" disabled>-- Please select an option --</b-form-select-option>
                              </template>
                            </b-form-select></b-form-group>
                          </b-list-group-item>
                        </span>
                        <b-list-group-item>
                          <label :for="'sb-inline' + product.productID">Quantity</label>
                          <b-form-spinbutton class="ml-2" :id="'sb-inline' + product.productID" v-model="quantity" inline></b-form-spinbutton>
                        </b-list-group-item>
                      </b-list-group>
                    </div>
                  </b-overlay>
                </b-sidebar>
                <!-- <span v-for="([key, values], index) in Object.entries(product.specifications)" :key="index">
                <b-form-group :label="'Choose '+key" v-if="Array.isArray(values)">
                  <b-form-checkbox-group
                    v-model="selector[key]"
                    :options="values"
                    size="sm"
                    stacked
                  >
                  </b-form-checkbox-group>
                </b-form-group></span> -->
              </div>
            </div>
            <hr>
            <div class="mx-auto bd-highlights text-center" v-if="productLoaded">
                <h3>Related Products</h3>
            </div>
        </b-container></b-overlay>
        <ProductScrollSection :products="relatedProducts"></ProductScrollSection>
    </b-container>
</template>
<script>
import ProductScrollSection from '../../components/ProductScrollSection.vue'
import { mapGetters, mapActions } from 'vuex'
import api from '../../plugins/axios'
import loader from '../../mixins/loader'

export default {
  name: 'CertainProduct',
  data () {
    return {
      // i: 0,
      // value: 3.5,
      // prevPrice: 500,
      // curPrice: 400,
      // discount: -20,
      // selected: null,
      // ProductName: 'Facebook',
      // coloroptions: ['red', 'green', 'yellow'],
      // sizeoptions: ['L', 'XL', 'XXL'],
      // images: [
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg',
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      // ],
      // totalimage: 2,
      // value: 4,
      // from here
      sliding: null,
      product: {},
      productLoaded: false,
      relatedProducts: [],
      selector: {},
      show: false,
      cartButtonPressed: null,
      quantity: 1
    }
  },
  computed: {
    ...mapGetters([
      'authToken'
    ]),
    allSelected () {
      if (!this.productLoaded) {
        return false
      }
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key]) && this.selector[key] === null) {
            return false
          }
        }
      }
      return true
    }
  },
  components: {
    ProductScrollSection
  },
  mixins: [
    loader
  ],
  methods: {
    ...mapActions([
      'addToCart'
    ]),
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    showPrev () {
      if (this.i === 0) this.i = this.totalimage
      this.i = (this.i - 1) % this.totalimage
    },
    showNext () {
      this.i = (this.i + 1) % this.totalimage
    },
    loadProduct (id) {
      this.loader('/product', { productID: id }, `product-${id}`)
        .then(response => {
          this.product = response.data
          this.product.specifications = this.product.extra
          delete this.product.extra
          this.productLoaded = true
          this.loadRelatedProducts()
          this.loadSelector()
        })
    },
    loadRelatedProducts () {
      api.post('/search', { productIds: this.product.relatedProductIDs })
        .then(response => {
          console.log(`products related to ${this.product.productID} loaded`)
          console.log(response)
          this.relatedProducts = response.data.products
        })
        .catch(error => {
          console.log(`products related to ${this.product.productID} failed loading`)
          console.log(error.response)
        })
    },
    loadSelector () {
      this.selector = JSON.parse(JSON.stringify(this.product.specifications)) // deep(disconnected) copy
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key])) {
            this.selector[key] = null
          }
        }
      }
      console.log('done')
    },
    makeOrder () {
      const curPrice = this.product.price.sellingPrice
      return {
        products: [{
          id: this.product.productID,
          name: this.product.name,
          specifications: this.selector,
          quantity: this.quantity,
          image: this.product.images[0],
          curPrice,
          discount: this.product.price.discount,
          vat: this.product.price.vat
        }],
        subTotal: curPrice * (1 + this.product.price.vat / 100) * this.quantity,
        deliveryCharge: 60
      }
    },
    initialHandler (cartButtonPressed) {
      if (!this.authToken && !cartButtonPressed) {
        this.$bvModal.show('login-modal')
      } else {
        this.loadProduct()
        this.show = true
        this.cartButtonPressed = cartButtonPressed
      }
    },
    finalHandler () {
      if (!this.allSelected) {
        return
      }
      if (this.cartButtonPressed) {
        this.addToCart([{
          productID: this.product.productID,
          specifications: this.selector,
          quantity: this.quantity,
          curPrice: this.product.price.sellingPrice,
          discount: this.product.price.discount,
          image: this.product.images[0],
          name: this.product.name,
          vat: this.product.price.vat
        }]).then().catch(error => {
          console.log(error.response)
        })
      } else {
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.makeOrder()
          }
        })
      }
      this.show = false
    },
    doReset () {
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key])) {
            this.selector[key] = null
          }
        }
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadProduct(to.params.id)
    }
  },
  created () {
    this.loadProduct(this.$route.params.id)
  }
}
</script>
